.popup {
  display: flex;
  position: fixed;
  top:0;
  right: 0;
  bottom: 0;
  left: 0;
  background-color: hsla(0, 0%, 0%, .5);
  align-items: center;
  justify-content: center;
  overflow: hidden;
  visibility: hidden;
  opacity: 0;
  transition: visibility .4s, opacity .4s ease-in-out;
}

.popup_image {
  background-color: hsla(0, 0%, 0%, .9);
}
